var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-container',{staticClass:"mt-3"},[_c('h2',[_vm._v("Projektinformation")]),_c('b-row',[_c('b-col',{attrs:{"cols":"7"}},[_c('div',{staticClass:"text-left"},[_c('p',[_vm._v(" Mit Karl Kraus (1874-1936) war eine zentrale Persönlichkeit der Wiener Moderne und als Schriftsteller und Publizist ungeheuer produktiv. Obwohl das Rechtssystem für ihn von zentraler Bedeutung war, sind seine umfangreichen Rechtsakten nahezu unerforscht. Sie stammen aus der Kanzlei seines langjährigen Anwaltes Oskar Samek, dokumentieren über 200 Fälle aus 17 Jahren (1922–1938) und befinden sich im Karl Kraus-Archiv der Wienbibliothek im Rathaus. ")]),_c('p',[_vm._v(" Das FWF-Projekt der "),_c('i',[_vm._v("Rechtsakten Karl Kraus")]),_vm._v(" baut aui das vorangegangene Projekt "),_c('i',[_vm._v("Karl Kraus Online – Die Rechtsperson")]),_vm._v(" (Ludwig Boltzmann Institut für Geschichte und Theorie der Biographie) auf. Katharina Prager hat von 2012 bis 2018 Bestände des Karl Kraus-Archivs in der Wienbibliothek im Rathaus neu geordnet und digitalisiert. Das Resultat war bis April 2022 auf der Plattform "),_c('i',[_vm._v("Karl Kraus Online")]),_vm._v(" zugänglich. Seit Mai 2022 löst die hier vorliegende Edition der Rechtsakten die alte Darstellung ab. Die unter dem Punkt "),_c('i',[_vm._v("Der Vorleser")]),_vm._v(" erstmals vollständig publizierten Vorlesungsprogramme Karl Kraus’ sind aber weiterhin unter folgender Adresse abrufbar: "),_c('a',{attrs:{"href":"https://www.kraus.wienbibliothek.at/der-vorleser","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("https://www.kraus.wienbibliothek.at/der-vorleser")]),_vm._v(". ")]),_c('p',[_vm._v(" Die Digitalisate der Rechtsakten aus der Kanzlei Oskar Samek wurden in dem im September 2018 begonnenen Forschungsprojekt im Volltext erschlossen, ediert und zum Teil auch schon ausgewertet. Die Ergebnisse liegen nun in Form dieser wissenschaftlichen Edition vor. ")]),_c('p',[_vm._v(" Die Digitalisate sind einer Transkription synoptisch gegenübergestellt. Der von der Text Encoding Initiative vorgegebene Standard zur Auszeichnung von XML-Dokumenten wurde für das Projekt und das zu edierende Material angepasst und optimiert. So ist die Auszeichnung der Texte und in der Folge die Herstellung von Bezügen zwischen den einzelnen Dokumenten und darüber hinaus ermöglicht. ")]),_c('p',[_vm._v(" Den Nutzerinnen und Nutzern wird der Zugang zu dem umfangreichen und komplexen Material in mehrerer Hinsicht erleichtert: ")]),_c('p',[_vm._v(" Über eine Auswahl verschiedener Themen (Kultur, Recht, Politik) kommen die Benutzerinnen und Benutzer zu bestimmten Vorauswahlen von Akten, die jeweils mit einem erläuternden Text der Herausgeber:innen versehen sind. ")]),_c('p',[_vm._v(" Zusätzlich gibt es die Möglichkeit, sich Dokumente nach handschriftlichen Kriterien gefiltert anzeigen zu lassen, da nachträgliche handschriftliche Überarbeitungen von besonderer Relevanz sein können. So kann unter anderem die Zusammenarbeit zwischen dem Anwalt Oskar Samek und seinem Mandanten Karl Kraus studiert werden. ")]),_c('p',[_vm._v("Daneben lässt sich auch eine Liste aller Akten aufrufen.")]),_c('p',[_vm._v(" Darüber hinaus können die Nutzerinnen und Nutzer über einen Handapparat auf mehrere Materialien zugreifen, welche die Beschäftigung mit den Akten zusätzlich unterstützen. Beiträge zu unterschiedlichen Aspekten der Edition und ihres Inhalts von Forschern unterschiedlicher Disziplinen bilden einen Teil. Ein anderer Teil ist Oskar Samek gewidmet und enthält einen biographischen Aufsatz sowie ein Interviewtranskript. Und im dritten Teil wird die durch Hermann Böhm verantwortete Print-Ausgabe der Rechtsakten aus den 1990er Jahren für die Forschung bereitgestellt. ")]),_c('p',[_vm._v(" Daneben erhalten die Nutzerinnen und Nutzer aber auch die Möglichkeit, über von "),_c('i',[_vm._v("Karl Kraus Online")]),_vm._v(" adaptierte, aktualisierte Statistiken eine 